import React from 'react';
import { Box, Button, Flex, Link, Image, Spacer } from '@chakra-ui/react';
import Discord from "./assets/social-media-icons/discord.svg";
import Opensea from "./assets/social-media-icons/opensea-logo.svg";
import Twitter from "./assets/social-media-icons/twitter-logo.svg";
import Logo from "./assets/social-media-icons/Untitled.png";

const NavBar = ({ accounts, setAccounts }) => {
    const isConnected = Boolean(accounts[0]);

    return (
        
        <Flex justify="space-between" align="center" padding="0" >

            {/* icons  */}

            <Link href='/' isExternal>
                <Image src={Logo} width={60} height={60} position="relative" left="0" top="0" />
                </Link>
                {/* <Box margin="0 90px" color="silver">FAQ</Box>
            <Spacer /> */}
        

            <Flex  justify="space-around"  padding="0 0">

            <Link href='https://twitter.com/DinosesNFT' isExternal>
                <Image src={Twitter} boxSize="60px" margin="0 3px" position="relative"   />
                </Link>
                
                <Link href='https://opensea.io/collection/dinos-nft-' isExternal>
                <Image src={Opensea} boxSize="60px" margin="0 3px" position="relative" />
                </Link>

                {/* <Link href='' isExternal>
                <Image src={Discord} boxSize="60px" margin="0 3px" position="relative" pointer-events="none" />
                </Link> */}
                
            </Flex>
            
        </Flex>
        
        
    )
};


export default NavBar;